body{
    margin: 0%;
    padding: 0%;
    background-color: rgb(0, 0, 0);
    font-family: Georgia, serif;
}

.App{
    margin: 10px 10px 10px 10px;
} 

@media only screen and (max-width: 606px) {
    .App{
        margin: 0px;
    } 
  }

.main-div{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.name-flag{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.name-flag > div{
    font-weight: bold;
    margin-left: 10px;
}

.flag-image{
    display: block;
    width: 70px;
    height: auto;
    border: 1px solid rgb(0, 0, 0);
}

/* ----------------- search box -------------------*/
.form-div{
    padding-bottom: 0px;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 50px;
    width: 90vw;
    border: 0;
    outline: 0;
}

.form-input-control{
    border-radius: 60vh;
    background-color: rgb(34, 31, 31);
    color: white;
    height: 100%;
    width: 100%;
    border: 0;
    outline: 0;
    padding: 0px 10px 0px 10px;
    font-weight: 500;
}
